import React, { useState, useEffect } from 'react';
import { Handle, Position } from '@xyflow/react';
import { Users, Calendar, CheckSquare, Clock, X, AlertCircle } from 'lucide-react';

const CaseNode = ({ data, selected }) => {
  const [isVisible, setIsVisible] = useState(data.skipAnimation || false);
  const [isHovered, setIsHovered] = useState(false);
  
  // Entry animation with slight delay for staggered effect - only if not skipping animations
  useEffect(() => {
    if (!data.skipAnimation) {
      const timer = setTimeout(() => setIsVisible(true), 150);
      return () => clearTimeout(timer);
    }
  }, [data.skipAnimation]);

  const getStatusColor = (status) => {
    switch (status) {
      case 'active': return 'text-emerald-500';
      case 'archived': return 'text-slate-500';
      case 'follow_up': return 'text-amber-500';
      default: return 'text-sky-500';
    }
  };

  const getStatusGradient = (status) => {
    switch (status) {
      case 'active': 
        return 'from-emerald-300 to-teal-500';
      case 'archived': 
        return 'from-slate-300 to-slate-500';
      case 'follow_up': 
        return 'from-amber-300 to-orange-500';
      default: 
        return 'from-sky-300 to-indigo-500';
    }
  };

  const getUrgencyLevel = (data) => {
    // Simple urgency calculation based on keywords
    const urgentKeywords = ['emergency', 'urgent', 'crisis', 'acute', 'severe'];
    const complaint = data.chief_complaint?.toLowerCase() || '';
    const diagnosis = data.diagnosis?.toLowerCase() || '';

    if (urgentKeywords.some(keyword => complaint.includes(keyword) || diagnosis.includes(keyword))) {
      return 'high';
    }
    return 'normal';
  };

  const urgency = getUrgencyLevel(data);
  const statusGradient = getStatusGradient(data.status);

  return (
    <div
      className={`group px-4 py-3 rounded-lg shadow-xl transition-all duration-600 min-w-[220px] relative transform 
        ${selected
          ? 'scale-105'
          : isHovered ? 'scale-[1.03]' : 'scale-100'
        } ${isVisible ? 'opacity-100 translate-y-0 rotate-0' : 'opacity-0 translate-y-6 rotate-1'}`}
      style={{
        transition: 'all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94), transform 0.3s ease',
        background: 'linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)',
        boxShadow: selected 
          ? '0 10px 25px -5px rgba(59, 130, 246, 0.4), 0 8px 10px -6px rgba(59, 130, 246, 0.3)' 
          : isHovered 
            ? '0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 8px 10px -6px rgba(0, 0, 0, 0.1)' 
            : '0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)',
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Connection Hotspots - Case-styled handles */}
      {/* Top border with gradient accent */}
      <div 
        className={`absolute top-0 left-0 w-full h-1.5 bg-gradient-to-r ${statusGradient} rounded-t-lg`}
      ></div>

      {/* Top handles */}
      <Handle
        id="top"
        type="source"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-blue-400 to-indigo-600`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 2,
          border: '2px solid white'
        }}
        isConnectable={true}
      />
      <Handle
        id="top"
        type="target"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-violet-400 to-purple-600`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 1,
          border: '2px solid white'
        }}
        isConnectable={true}
      />

      {/* Right handles */}
      <Handle
        id="right"
        type="source"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-blue-400 to-indigo-600`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 2,
          border: '2px solid white'
        }}
        isConnectable={true}
      />
      <Handle
        id="right"
        type="target"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-violet-400 to-purple-600`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 1,
          border: '2px solid white'
        }}
        isConnectable={true}
      />

      {/* Bottom handles */}
      <Handle
        id="bottom"
        type="source"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-blue-400 to-indigo-600`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 2,
          border: '2px solid white'
        }}
        isConnectable={true}
      />
      <Handle
        id="bottom"
        type="target"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-violet-400 to-purple-600`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 1,
          border: '2px solid white'
        }}
        isConnectable={true}
      />

      {/* Left handles */}
      <Handle
        id="left"
        type="source"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-blue-400 to-indigo-600`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 2,
          border: '2px solid white'
        }}
        isConnectable={true}
      />
      <Handle
        id="left"
        type="target"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 transform hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}
          shadow-md bg-gradient-to-br from-violet-400 to-purple-600`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 1,
          border: '2px solid white'
        }}
        isConnectable={true}
      />
      
      {/* Selected/hover state effects */}
      {(selected || isHovered) && (
        <div className={`absolute inset-0 rounded-lg pointer-events-none ${selected ? 'animate-pulse-subtle' : ''}`} 
          style={{
            boxShadow: `0 0 0 2px rgba(59, 130, 246, ${selected ? '0.5' : '0.3'})`,
            animation: selected ? 'pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite' : 'none',
          }} 
        />
      )}

      {/* Urgency indicator - enhanced with gradient & animation */}
      {urgency === 'high' && (
        <div className="absolute -top-2 -right-2 z-10">
          <div className="w-6 h-6 bg-gradient-to-br from-red-400 to-pink-600 rounded-full flex items-center justify-center border-2 border-white animate-pulse shadow-md">
            <AlertCircle size={14} className="text-white drop-shadow-sm" />
          </div>
        </div>
      )}

      <div className="flex items-center gap-2 mt-4 mb-2 relative">
        <div className="flex items-center gap-1">
          <div className={`p-1 rounded-full bg-gradient-to-r ${statusGradient} shadow-inner`}>
            <Users size={16} className="text-white drop-shadow-sm" />
          </div>
          {data.linked_topics && data.linked_topics.length > 0 && (
            <div className="text-xs bg-white/80 backdrop-blur-sm text-gray-700 px-2 py-0.5 rounded-full shadow-sm font-medium">
              {data.linked_topics.length}
            </div>
          )}
        </div>
        <div className="font-semibold text-sm text-gray-800 truncate flex-1">{data.label}</div>
        {data.onDelete && (
          <button
            onClick={(e) => {
              // Don't stop propagation immediately - delay the action to allow double-clicks
              e.preventDefault(); // Prevent any default behavior but allow bubbling
              
              // Use a longer timeout to distinguish between single click and double-click
              const deleteTimeout = setTimeout(() => {
                // Only execute delete if this wasn't part of a double-click sequence
                e.stopPropagation(); // Stop propagation only when actually deleting
                data.onDelete();
              }, 300); // Increased timeout
              
              // Store timeout ID on the button to cancel it if double-click occurs
              e.currentTarget.deleteTimeout = deleteTimeout;
            }}
            onDoubleClick={(e) => {
              // Cancel the pending delete action
              if (e.currentTarget.deleteTimeout) {
                clearTimeout(e.currentTarget.deleteTimeout);
                e.currentTarget.deleteTimeout = null;
              }
              
              // Don't prevent default - allow the double-click to bubble up for modal opening
              // The parent node will handle the double-click to open the modal
            }}
            className="ml-auto p-1.5 hover:bg-red-50 rounded-full transition-all duration-200 hover:scale-110 opacity-70 hover:opacity-100 hover:text-red-500"
          >
            <X size={14} className="drop-shadow-sm" />
          </button>
        )}
      </div>

      <div className="text-xs text-gray-600 mb-3 font-medium truncate backdrop-blur-sm bg-white/50 rounded px-2 py-1">
        {data.diagnosis}
      </div>

      {/* Enhanced patient info */}
      <div className="space-y-2">
        {data.age && (
          <div className="text-xs font-medium flex items-center gap-1.5">
            <Calendar size={12} className={getStatusColor(data.status)} />
            <span>Age: <span className="font-semibold">{data.age}</span></span>
          </div>
        )}

        {/* Status badge */}
        {data.status && (
          <span className={`inline-block px-2.5 py-1 rounded-full text-xs font-medium shadow-sm bg-white/70 backdrop-blur-sm ${getStatusColor(data.status)}`}>
            {data.status.replace('_', ' ')}
          </span>
        )}
      </div>

      {/* Progress indicators with enhanced styling */}
      {data.tasks_count && data.tasks_count > 0 && (
        <div className="mt-3 mb-1">
          <div className="flex justify-between items-center text-xs mb-1">
            <div className="flex items-center gap-1.5">
              <CheckSquare size={12} className={getStatusColor(data.status)} />
              <span className="font-medium">{data.completed_tasks || 0}/{data.tasks_count} tasks</span>
            </div>
            <span className="font-semibold">
              {Math.round((data.completed_tasks || 0) / data.tasks_count * 100)}%
            </span>
          </div>
          <div className="w-full h-1.5 bg-gray-200 rounded-full overflow-hidden">
            <div 
              className={`h-full transition-all duration-1000 bg-gradient-to-r ${statusGradient}`}
              style={{ width: `${(data.completed_tasks || 0) / data.tasks_count * 100}%` }}
            ></div>
          </div>
        </div>
      )}

      {/* Last updated indicator - enhanced styling */}
      {data.updated_at && (
        <div className="absolute bottom-1.5 right-1.5 opacity-60 flex items-center gap-1 text-[10px] text-gray-500 font-medium">
          <Clock size={10} />
          <span>updated</span>
        </div>
      )}
    </div>
  );
};

export default CaseNode;