import React, { memo } from 'react';
import { BaseEdge, getBezierPath, EdgeLabelRenderer } from '@xyflow/react';

/**
 * Enhanced Edge Component with Label Support
 * 
 * This edge component supports labels and uses proper prop destructuring
 * to prevent React Flow-specific props from reaching DOM elements.
 */
const EnhancedEdge = memo(({
  id,
  source,
  target,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
  style = {},
  markerEnd,
  markerStart,
  label,
  // Destructure React Flow-specific props to prevent them from reaching DOM
  sourceHandleId,
  targetHandleId,
  data,
  selected,
  animated,
  // Don't pass any other props to BaseEdge
}) => {
  const [edgePath, labelX, labelY] = getBezierPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  // Determine connection type based on source and target node types
  const getEdgeStyle = (sourceType, targetType, isSelected) => {
    // Default style
    const defaultStyle = {
      stroke: '#64748b',
      strokeWidth: 3,
      strokeLinecap: 'round',
      strokeOpacity: 0.95,
      filter: 'drop-shadow(0 1px 2px rgba(0,0,0,0.1))',
    };

    // Connection types based on node types
    const sourceNodeType = sourceType?.split('_')[0] || '';
    const targetNodeType = targetType?.split('_')[0] || '';

    // Case-to-Topic connection (blue to purple gradient)
    if (sourceNodeType === 'case' && targetNodeType === 'topic') {
      return {
        ...defaultStyle,
        stroke: 'url(#case-to-topic-gradient)',
        strokeWidth: 3.5,
        strokeDasharray: '',
      };
    }

    // Topic-to-Literature connection (purple to pink gradient)
    if (sourceNodeType === 'topic' && targetNodeType === 'literature') {
      return {
        ...defaultStyle,
        stroke: 'url(#topic-to-literature-gradient)',
        strokeWidth: 3,
        strokeDasharray: '',
      };
    }

    // Topic-to-Task connection (purple to amber gradient)
    if (sourceNodeType === 'topic' && targetNodeType === 'task') {
      return {
        ...defaultStyle,
        stroke: 'url(#topic-to-task-gradient)',
        strokeWidth: 3,
        strokeDasharray: '8,4',
      };
    }

    return {
      ...defaultStyle,
      ...(isSelected ? { strokeWidth: 4, filter: 'drop-shadow(0 2px 4px rgba(0,0,0,0.2))' } : {}),
      ...style
    };
  };

  return (
    <>
      {/* SVG gradient definitions for edge styling */}
      <defs>
        <linearGradient id="case-to-topic-gradient" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop offset="0%" stopColor="#3b82f6" />
          <stop offset="100%" stopColor="#8b5cf6" />
        </linearGradient>
        <linearGradient id="topic-to-literature-gradient" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop offset="0%" stopColor="#8b5cf6" />
          <stop offset="100%" stopColor="#d946ef" />
        </linearGradient>
        <linearGradient id="topic-to-task-gradient" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop offset="0%" stopColor="#8b5cf6" />
          <stop offset="100%" stopColor="#f59e0b" />
        </linearGradient>
      </defs>
      
      {/* Glow effect for selected edges */}
      {selected && (
        <BaseEdge
          path={edgePath}
          style={{
            stroke: 'rgba(255, 255, 255, 0.6)',
            strokeWidth: 8,
            strokeLinecap: 'round',
            strokeOpacity: 0.5,
            filter: 'blur(4px)',
          }}
        />
      )}
      
      <BaseEdge
        id={id}
        path={edgePath}
        style={getEdgeStyle(source, target, selected)}
        markerEnd={markerEnd}
        markerStart={markerStart}
      />
      
      {label && (
        <EdgeLabelRenderer>
          <div
            style={{
              position: 'absolute',
              transform: `translate(-50%, -50%) translate(${labelX}px,${labelY}px)`,
              fontSize: 12,
              pointerEvents: 'all',
              backdropFilter: 'blur(4px)',
              transition: 'all 0.3s ease',
            }}
            className={`nodrag nopan px-2.5 py-1 rounded-full shadow-md text-xs font-medium
              ${selected ? 'bg-indigo-100 text-indigo-700 ring-2 ring-indigo-200' : 'bg-white/90 text-gray-700'}`}
          >
            {label}
          </div>
        </EdgeLabelRenderer>
      )}
    </>
  );
});

EnhancedEdge.displayName = 'EnhancedEdge';

export default EnhancedEdge;
