import React, { useState, useEffect } from 'react';
import { Handle, Position } from '@xyflow/react';
import { CheckSquare, X, Clock } from 'lucide-react';

const TaskNode = ({ data, selected }) => {
  const [isVisible, setIsVisible] = useState(data.skipAnimation || false);
  const [isHovered, setIsHovered] = useState(false);
  
  // Entry animation with different timing for variety - only if not skipping animations
  useEffect(() => {
    if (!data.skipAnimation) {
      const timer = setTimeout(() => setIsVisible(true), 200);
      return () => clearTimeout(timer);
    }
  }, [data.skipAnimation]);

  // Define gradient backgrounds based on status
  const getStatusGradient = (status) => {
    switch (status) {
      case 'pending':
        return 'from-amber-400 to-amber-600';
      case 'in_progress':
        return 'from-sky-400 to-blue-600';
      case 'completed':
        return 'from-emerald-400 to-green-600';
      default:
        return 'from-gray-400 to-gray-600';
    }
  };
  
  const gradient = getStatusGradient(data.status);
  
  // Priority background indicator
  const getPriorityAccent = (priority) => {
    switch (priority?.toLowerCase()) {
      case 'high':
        return 'from-red-400 to-red-600';
      case 'medium':
        return 'from-orange-400 to-orange-600';
      case 'low':
        return 'from-emerald-400 to-emerald-600';
      default:
        return 'from-gray-400 to-gray-600';
    }
  };

  return (
    <div
      className={`group px-6 py-3 rounded-full shadow-xl transition-all duration-500 text-white relative transform 
        ${selected ? 'scale-105' : isHovered ? 'scale-[1.02]' : 'scale-100'} 
        ${isVisible ? 'opacity-100 translate-x-0' : 'opacity-0 scale-85 -translate-x-4'}`}
      style={{
        background: `linear-gradient(135deg, ${data.status === 'completed' ? 'rgba(16, 185, 129, 0.85)' : 'rgba(59, 130, 246, 0.95)'} 0%, ${data.status === 'completed' ? 'rgba(5, 150, 105, 0.95)' : 'rgba(99, 102, 241, 0.98)'} 100%)`,
        boxShadow: selected 
          ? '0 10px 25px -5px rgba(99, 102, 241, 0.5), 0 8px 10px -6px rgba(99, 102, 241, 0.5)' 
          : isHovered 
            ? '0 10px 25px -5px rgba(99, 102, 241, 0.3), 0 8px 10px -6px rgba(99, 102, 241, 0.3)' 
            : '0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.1)',
        transition: 'all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55), transform 0.3s ease, box-shadow 0.3s ease',
        backdropFilter: 'blur(8px)',
        minWidth: '240px',
        maxWidth: '300px'
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Priority indicator on the left end of the pill */}
      <div 
        className={`absolute left-0 top-0 bottom-0 w-2 rounded-l-full bg-gradient-to-b ${getPriorityAccent(data.priority)}`}
      ></div>

      {/* Connection Hotspots - Pill-style handles */}
      {/* Top handles with glow effect */}
      <Handle
        id="top"
        type="source"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(99, 102, 241)',
          boxShadow: '0 0 8px 2px rgba(99, 102, 241, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="top"
        type="target"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(124, 58, 237)',
          boxShadow: '0 0 8px 2px rgba(124, 58, 237, 0.7)'
        }}
        isConnectable={true}
      />

      {/* Right handles with glow effect */}
      <Handle
        id="right"
        type="source"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(99, 102, 241)',
          boxShadow: '0 0 8px 2px rgba(99, 102, 241, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="right"
        type="target"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(124, 58, 237)',
          boxShadow: '0 0 8px 2px rgba(124, 58, 237, 0.7)'
        }}
        isConnectable={true}
      />

      {/* Bottom handles with glow effect */}
      <Handle
        id="bottom"
        type="source"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(99, 102, 241)',
          boxShadow: '0 0 8px 2px rgba(99, 102, 241, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="bottom"
        type="target"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(124, 58, 237)',
          boxShadow: '0 0 8px 2px rgba(124, 58, 237, 0.7)'
        }}
        isConnectable={true}
      />

      {/* Left handles with glow effect - positioned away from priority indicator */}
      <Handle
        id="left"
        type="source"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          left: '10px',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(99, 102, 241)',
          boxShadow: '0 0 8px 2px rgba(99, 102, 241, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="left"
        type="target"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          left: '10px',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(124, 58, 237)',
          boxShadow: '0 0 8px 2px rgba(124, 58, 237, 0.7)'
        }}
        isConnectable={true}
      />
      
      {/* Selected/hover state ring effect */}
      {(selected || isHovered) && (
        <div 
          className={`absolute inset-0 rounded-full pointer-events-none ${selected ? 'animate-pulse-subtle' : ''}`}
          style={{
            boxShadow: `0 0 0 2px rgba(255, 255, 255, ${selected ? '0.5' : '0.3'})`,
            animation: selected ? 'pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite' : 'none',
          }}
        />
      )}

      {/* Task content - refined for pill layout */}
      <div className="flex items-center">
        <div className="flex-shrink-0 mr-3">
          <div className={`p-1 rounded-full bg-white/20 shadow-inner ${data.status === 'completed' ? 'bg-white/40' : ''}`}>
            <CheckSquare size={18} className="drop-shadow-sm" />
          </div>
        </div>
        
        <div className="flex-grow min-w-0">
          <div className="font-semibold text-sm truncate">{data.label}</div>
          
          {/* Status and due date info */}
          <div className="flex items-center gap-2 text-xs mt-0.5">
            {data.due_date && (
              <div className="flex items-center gap-1 bg-white/20 px-2 py-0.5 rounded-full">
                <Clock size={10} />
                <span>{new Date(data.due_date).toLocaleDateString()}</span>
              </div>
            )}
          </div>
        </div>
        
        {/* Delete button */}
        {data.onDelete && (
          <button
            onClick={(e) => {
              e.preventDefault();
              const deleteTimeout = setTimeout(() => {
                e.stopPropagation();
                data.onDelete();
              }, 300);
              e.currentTarget.deleteTimeout = deleteTimeout;
            }}
            onDoubleClick={(e) => {
              if (e.currentTarget.deleteTimeout) {
                clearTimeout(e.currentTarget.deleteTimeout);
                e.currentTarget.deleteTimeout = null;
              }
            }}
            className="ml-2 p-1.5 hover:bg-white/20 rounded-full transition-all duration-200 hover:scale-110 opacity-70 hover:opacity-100"
          >
            <X size={14} className="drop-shadow-sm" />
          </button>
        )}
      </div>
    </div>
  );
};

export default TaskNode;