import React, { useState, useEffect } from 'react';
import { Handle, Position } from '@xyflow/react';
import { BookOpen, X, Calendar } from 'lucide-react';

const LiteratureNode = ({ data, selected }) => {
  const [isVisible, setIsVisible] = useState(data.skipAnimation || false);
  const [isHovered, setIsHovered] = useState(false);
  
  // Entry animation with the longest delay for final polish - only if not skipping animations
  useEffect(() => {
    if (!data.skipAnimation) {
      const timer = setTimeout(() => setIsVisible(true), 250);
      return () => clearTimeout(timer);
    }
  }, [data.skipAnimation]);

  return (
    <div
      className={`group transition-all duration-650 relative transform 
        ${selected ? 'scale-105' : isHovered ? 'scale-[1.02]' : 'scale-100'}
        ${isVisible ? 'opacity-100 translate-x-0 rotate-0' : 'opacity-0 scale-95 translate-x-4 -rotate-1'}`}
      style={{
        width: '130px',
        height: '180px',
        background: 'linear-gradient(135deg, #f3e8ff 0%, #ddd6fe 100%)',
        borderRadius: '8px',
        boxShadow: selected
          ? '0 10px 30px -5px rgba(139, 92, 246, 0.5), 0 8px 10px -6px rgba(139, 92, 246, 0.4)'
          : isHovered
            ? '0 10px 25px -5px rgba(139, 92, 246, 0.3), 0 8px 10px -6px rgba(139, 92, 246, 0.2)'
            : '0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.05)',
        transition: 'all 0.65s cubic-bezier(0.175, 0.885, 0.32, 1.275), transform 0.3s ease, box-shadow 0.3s ease',
        borderTop: '8px solid',
        borderImage: 'linear-gradient(to right, #8b5cf6, #6d28d9) 1',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        overflow: 'hidden',
        transform: isHovered && !selected ? 'rotate(1deg)' : selected ? 'rotate(0deg)' : 'rotate(0deg)'
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Connection Hotspots for Literature Card - vertical placement */}
      {/* Top handles */}
      <Handle
        id="top"
        type="source"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(139, 92, 246)',
          boxShadow: '0 0 8px 2px rgba(139, 92, 246, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="top"
        type="target"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(109, 40, 217)',
          boxShadow: '0 0 8px 2px rgba(109, 40, 217, 0.7)'
        }}
        isConnectable={true}
      />

      {/* Right handles */}
      <Handle
        id="right"
        type="source"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(139, 92, 246)',
          boxShadow: '0 0 8px 2px rgba(139, 92, 246, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="right"
        type="target"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(109, 40, 217)',
          boxShadow: '0 0 8px 2px rgba(109, 40, 217, 0.7)'
        }}
        isConnectable={true}
      />

      {/* Bottom handles */}
      <Handle
        id="bottom"
        type="source"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(139, 92, 246)',
          boxShadow: '0 0 8px 2px rgba(139, 92, 246, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="bottom"
        type="target"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(109, 40, 217)',
          boxShadow: '0 0 8px 2px rgba(109, 40, 217, 0.7)'
        }}
        isConnectable={true}
      />

      {/* Left handles */}
      <Handle
        id="left"
        type="source"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 2,
          border: '2px solid white',
          background: 'rgb(139, 92, 246)',
          boxShadow: '0 0 8px 2px rgba(139, 92, 246, 0.7)'
        }}
        isConnectable={true}
      />
      <Handle
        id="left"
        type="target"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 1,
          border: '2px solid white',
          background: 'rgb(109, 40, 217)',
          boxShadow: '0 0 8px 2px rgba(109, 40, 217, 0.7)'
        }}
        isConnectable={true}
      />
      
      {/* Selected/hover state ring effect */}
      {(selected || isHovered) && (
        <div 
          className={`absolute inset-0 rounded-lg pointer-events-none ${selected ? 'animate-pulse-subtle' : ''}`}
          style={{
            boxShadow: `0 0 0 2px rgba(139, 92, 246, ${selected ? '0.5' : '0.3'})`,
            animation: selected ? 'pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite' : 'none',
          }}
        />
      )}

      {/* Literature Card Content - Vertical layout */}
      <div className="flex flex-col h-full">
        {/* Card Header with book icon */}
        <div 
          className="px-3 pt-3 pb-2 cursor-pointer"
          onClick={(e) => {
            e.preventDefault();
            const clickTimeout = setTimeout(() => {
              e.stopPropagation();
              if (data.onLiteratureClick) {
                data.onLiteratureClick(data);
              }
            }, 300);
            e.currentTarget.clickTimeout = clickTimeout;
          }}
          onDoubleClick={(e) => {
            if (e.currentTarget.clickTimeout) {
              clearTimeout(e.currentTarget.clickTimeout);
              e.currentTarget.clickTimeout = null;
            }
          }}
        >
          <div className="flex justify-center mb-2">
            <div className="p-1.5 rounded-full bg-purple-100 shadow-inner">
              <BookOpen size={20} className="text-purple-600" />
            </div>
          </div>
          <div className="text-center font-semibold text-sm text-gray-800 line-clamp-3 mb-1">
            {data.label}
          </div>
        </div>

        {/* Card Body */}
        <div className="px-3 flex-grow flex flex-col justify-end">
          {/* Author info - truncated for vertical card */}
          <div className="text-xs text-center text-gray-600 mb-1 line-clamp-2">
            {data.authors}
          </div>
          
          {/* Year with calendar icon */}
          <div className="flex items-center justify-center text-xs text-purple-700 font-medium mt-1 mb-3">
            <Calendar size={10} className="mr-1" />
            {data.year}
          </div>
        </div>

        {/* Delete button - positioned absolutely */}
        {data.onDelete && (
          <button
            onClick={(e) => {
              e.preventDefault();
              const deleteTimeout = setTimeout(() => {
                e.stopPropagation();
                data.onDelete();
              }, 300);
              e.currentTarget.deleteTimeout = deleteTimeout;
            }}
            onDoubleClick={(e) => {
              if (e.currentTarget.deleteTimeout) {
                clearTimeout(e.currentTarget.deleteTimeout);
                e.currentTarget.deleteTimeout = null;
              }
            }}
            className="absolute top-0.5 right-0.5 p-1 hover:bg-purple-200 rounded-full transition-all duration-200 hover:scale-110 opacity-70 hover:opacity-100"
          >
            <X size={12} className="text-purple-700" />
          </button>
        )}
      </div>
    </div>
  );
};

export default LiteratureNode;