import React, { useState, useEffect } from 'react';
import { Handle, Position } from '@xyflow/react';
import { Brain, Tag, Clock, X } from 'lucide-react';

const TopicNode = ({ data, selected }) => {
  const [isVisible, setIsVisible] = useState(data.skipAnimation || false);
  const [isHovered, setIsHovered] = useState(false);
  
  // Entry animation - only if not skipping animations
  useEffect(() => {
    if (!data.skipAnimation) {
      const timer = setTimeout(() => setIsVisible(true), 100);
      return () => clearTimeout(timer);
    }
  }, [data.skipAnimation]);

  // Define a secondary color that's complementary to the main color
  const getSecondaryColor = (primaryColor) => {
    const colors = {
      '#3B82F6': '#6366F1', // blue to indigo
      '#10B981': '#059669', // emerald to green
      '#8B5CF6': '#7C3AED', // violet to purple
      '#EC4899': '#DB2777', // pink to rose
      '#F59E0B': '#D97706', // amber to yellow
      '#6B7280': '#4B5563', // gray to cool gray
      '#EF4444': '#DC2626', // red to slightly darker red
    };
    
    return colors[primaryColor] || '#6366F1'; // Default to indigo if no match
  };

  const completionPercentage = data.flashcard_count > 0
    ? ((data.completed_flashcards || 0) / data.flashcard_count) * 100
    : 0;
    
  const primaryColor = data.color || '#3B82F6';
  const secondaryColor = getSecondaryColor(primaryColor);

  return (
    <div
      className={`group transition-all duration-700 relative transform 
        ${selected ? 'scale-110' : isHovered ? 'scale-105' : 'scale-100'}
        ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 scale-75 translate-y-8'}`}
      style={{
        width: '160px',
        height: '160px',
        borderRadius: '50%',
        background: `radial-gradient(circle at 30% 30%, ${primaryColor} 0%, ${secondaryColor} 100%)`,
        color: 'white',
        boxShadow: selected
          ? `0 0 30px 5px ${primaryColor}80, 0 8px 32px rgba(0,0,0,0.4), inset 0 -3px 8px rgba(255,255,255,0.2)`
          : isHovered
            ? `0 10px 30px -5px ${primaryColor}60, 0 8px 16px rgba(0,0,0,0.2), inset 0 -3px 8px rgba(255,255,255,0.2)`
            : `0 8px 25px -5px ${primaryColor}40, 0 4px 12px rgba(0,0,0,0.1), inset 0 -3px 8px rgba(255,255,255,0.1)`,
        transition: 'all 0.7s cubic-bezier(0.34, 1.56, 0.64, 1), transform 0.3s ease, box-shadow 0.4s ease',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '1rem',
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Connection Hotspots - Circular handles for circular node */}
      {/* Top handles with glow effect */}
      <Handle
        id="top"
        type="source"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 2,
          border: '2px solid white',
          background: primaryColor,
          boxShadow: `0 0 10px 2px ${primaryColor}80`
        }}
        isConnectable={true}
      />
      <Handle
        id="top"
        type="target"
        position={Position.Top}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(-50%)',
          zIndex: 1,
          border: '2px solid white',
          background: secondaryColor,
          boxShadow: `0 0 10px 2px ${secondaryColor}80`
        }}
        isConnectable={true}
      />

      {/* Right handles with glow effect */}
      <Handle
        id="right"
        type="source"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 2,
          border: '2px solid white',
          background: primaryColor,
          boxShadow: `0 0 10px 2px ${primaryColor}80`
        }}
        isConnectable={true}
      />
      <Handle
        id="right"
        type="target"
        position={Position.Right}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(50%)',
          zIndex: 1,
          border: '2px solid white',
          background: secondaryColor,
          boxShadow: `0 0 10px 2px ${secondaryColor}80`
        }}
        isConnectable={true}
      />

      {/* Bottom handles with glow effect */}
      <Handle
        id="bottom"
        type="source"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 2,
          border: '2px solid white',
          background: primaryColor,
          boxShadow: `0 0 10px 2px ${primaryColor}80`
        }}
        isConnectable={true}
      />
      <Handle
        id="bottom"
        type="target"
        position={Position.Bottom}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          left: '50%',
          transform: 'translateX(-50%) translateY(50%)',
          zIndex: 1,
          border: '2px solid white',
          background: secondaryColor,
          boxShadow: `0 0 10px 2px ${secondaryColor}80`
        }}
        isConnectable={true}
      />

      {/* Left handles with glow effect */}
      <Handle
        id="left"
        type="source"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 2,
          border: '2px solid white',
          background: primaryColor,
          boxShadow: `0 0 10px 2px ${primaryColor}80`
        }}
        isConnectable={true}
      />
      <Handle
        id="left"
        type="target"
        position={Position.Left}
        className={`w-3 h-3 rounded-full transition-all duration-300 hover:scale-150 cursor-pointer 
          ${selected || isHovered ? 'opacity-100' : 'opacity-0'}`}
        style={{
          top: '50%',
          transform: 'translateY(-50%) translateX(-50%)',
          zIndex: 1,
          border: '2px solid white',
          background: secondaryColor,
          boxShadow: `0 0 10px 2px ${secondaryColor}80`
        }}
        isConnectable={true}
      />
      
      {/* Selected/hover state ring effect */}
      {(selected || isHovered) && (
        <div 
          className={`absolute inset-0 rounded-full pointer-events-none ${selected ? 'animate-pulse-subtle' : ''}`}
          style={{
            boxShadow: `0 0 0 3px rgba(255, 255, 255, ${selected ? '0.5' : '0.3'})`,
            animation: selected ? 'pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite' : 'none',
          }}
        />
      )}

      {/* Priority indicator - enhanced positioning for circle */}
      {data.priority && (
        <div className="absolute -top-2 -right-2 z-10">
          <div className={`w-7 h-7 rounded-full flex items-center justify-center text-xs font-bold border-2 border-white shadow-lg ${
              data.priority === 'high' ? 'bg-gradient-to-br from-red-400 to-red-600' :
              data.priority === 'medium' ? 'bg-gradient-to-br from-amber-400 to-amber-600' : 
              'bg-gradient-to-br from-emerald-400 to-emerald-600'
            }`}>
            {data.priority === 'high' ? '!' : data.priority === 'medium' ? '' : ''}
          </div>
        </div>
      )}

      {/* Brain icon at the top */}
      <div className="mb-2 relative">
        <div className="p-2 rounded-full bg-white/20 shadow-inner mb-1">
          <Brain size={28} className="drop-shadow-lg" />
        </div>
      </div>

      {/* Main topic label - centered */}
      <div className="font-semibold text-lg text-center mb-1 px-1">
        {data.label}
      </div>

      {/* Category - smaller and centered */}
      <div className="text-xs opacity-90 text-center mb-2 px-1">
        {data.category}
      </div>

      {/* Delete button - positioned in absolute to maintain circular design */}
      {data.onDelete && (
        <button
          onClick={(e) => {
            e.preventDefault();
            const deleteTimeout = setTimeout(() => {
              e.stopPropagation();
              data.onDelete();
            }, 300);
            e.currentTarget.deleteTimeout = deleteTimeout;
          }}
          onDoubleClick={(e) => {
            if (e.currentTarget.deleteTimeout) {
              clearTimeout(e.currentTarget.deleteTimeout);
              e.currentTarget.deleteTimeout = null;
            }
          }}
          className="absolute -bottom-1 -right-1 p-1.5 bg-white/30 hover:bg-white/50 rounded-full 
            transition-all duration-200 hover:scale-110 opacity-70 hover:opacity-100 backdrop-blur-sm"
        >
          <X size={14} className="drop-shadow-sm" />
        </button>
      )}

      {/* Circular progress indicator */}
      {data.flashcard_count > 0 && (
        <div className="relative w-12 h-12 mx-auto mt-1">
          {/* Background circle */}
          <svg className="w-full h-full" viewBox="0 0 36 36">
            <circle
              cx="18" cy="18" r="16"
              fill="none"
              className="stroke-white/30"
              strokeWidth="3"
            />
            {/* Progress arc */}
            <circle
              cx="18" cy="18" r="16"
              fill="none"
              className="stroke-white"
              strokeWidth="3"
              strokeDasharray={`${completionPercentage * 100.5 / 100} 100`}
              strokeDashoffset="25"
              strokeLinecap="round"
              transform="rotate(-90 18 18)"
            />
            {/* Percentage text */}
            <text
              x="18" y="20"
              textAnchor="middle"
              fontSize="10"
              fill="white"
              fontWeight="bold"
            >
              {Math.round(completionPercentage)}%
            </text>
          </svg>
          
          {/* Tag count indicator */}
          {data.tags && data.tags.length > 0 && (
            <div className="absolute -bottom-1 -right-1 w-6 h-6 bg-white/30 backdrop-blur-sm rounded-full 
              flex items-center justify-center shadow-sm">
              <Tag size={10} />
              <span className="text-[8px] font-bold ml-0.5">{data.tags.length}</span>
            </div>
          )}
        </div>
      )}

      {/* Last updated indicator - repositioned for circle */}
      {data.updated_at && (
        <div className="absolute -bottom-1 -left-1 w-5 h-5 rounded-full flex items-center justify-center bg-white/20 backdrop-blur-sm">
          <Clock size={10} className="opacity-80" />
        </div>
      )}
    </div>
  );
};

export default TopicNode;